/*----------------------------------------------------------
# ACF Flexible Content Improved Styling
----------------------------------------------------------*/
.acf-fields .acf-fc-thumbnail {
  position: absolute;
  background-color: #fff;
  border: 1px solid #e1e1e1;
  bottom: 7px;
  left: 39px;
  width: 50px;
  padding: 3px;
  box-sizing: border-box;
}

.acf-fields .acf-fc-thumbnail img {
  display: block;
  width: 100%;
}

.acf-fields .acf-flexible-content .layout {
  margin-top: 25px;
  background: #f9f9f9;
}

.acf-fields .acf-flexible-content .layout .acf-fc-layout-handle {
  background: #f2f2f2;
}

.acf-fields .acf-flexible-content .layout .acf-fc-layout-order {
  border-radius: 50%;
  border: 1px solid #e1e1e1;
  background: #fff;
}

.acf-fields .acf-fc-title {
  display: inline-block;
  margin: 0 0 0 20px;
}

.acf-fields .acf-flexible-content {
  margin-top: 25px;
}

.acf-fc-popup-text {
  display: block;
  line-height: 1;
  margin-left: 15px;
}

.wp-admin .acf-fc-popup a {
  display: flex;
  align-items: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.wp-admin .acf-fc-popup a:hover {
  background: rgba(255, 255, 255, 0.08);
}

.wp-admin .acf-fc-popup ul li:last-child a {
  border-bottom: none;
}

/*----------------------------------------------------------
# ACF SVG Field
----------------------------------------------------------*/
#toplevel_page_fws-settings .dashicons-before > img {
  padding-top: 5px;
}

#toplevel_page_fws-settings .current img {
  opacity: 1;
}

#poststuff .fws-checkbox-icons-list,
.fws-radio-icons-list {
  display: flex;
  flex-flow: wrap;
}

#poststuff .fws-checkbox-icons-list li,
.fws-radio-icons-list li {
  width: 33%;
  display: flex;
  align-items: center;
  margin: 3px 0;
}

@media screen and (max-width: 1650px) {
  #poststuff .fws-checkbox-icons-list li,
  .fws-radio-icons-list li {
    width: 50%;
  }
}

@media screen and (max-width: 1220px) {
  #poststuff .fws-checkbox-icons-list li,
  .fws-radio-icons-list li {
    width: 100%;
  }
}

#poststuff .fws-checkbox-icons-list li label,
.fws-radio-icons-list li label {
  width: 100%;
  display: flex;
  align-items: center;
}

#poststuff .fws-checkbox-icons-list input,
.fws-radio-icons-list input {
  position: absolute;
  left: 0;
}

#poststuff .fws-checkbox-icons-list .acf-checkbox-toggle,
.fws-radio-icons-list .acf-checkbox-toggle {
  position: relative;
}

.fws-checkbox-icons-list__icon {
  height: 15px;
  left: 29px;
  display: block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.fws-radio-icons-list__name,
.fws-checkbox-icons-list__name {
  transform: translateX(54px);
}

.fws-svg-icon .acf-input-prepend {
  float: none;
  border-right-width: 1px;
  border-radius: 3px;
  width: 100px;
  height: 100px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  visibility: hidden;
  opacity: 0;
  transition: 200ms ease;
}

.fws-svg-icon .acf-input-prepend.is-active {
  visibility: visible;
  opacity: 1;
}

.fws-svg-icon .acf-input-prepend .acf__admin-icon-svg {
  font-size: 50px;
}

.fws-svg-icon .acf-input-wrap {
  width: 100px;
  visibility: hidden;
  opacity: 0;
  transition: 200ms ease;
}

.fws-svg-icon .acf-input-wrap.is-active {
  visibility: visible;
  opacity: 1;
}

.fws-svg-icon .acf-input-wrap input.acf-is-prepended {
  display: block;
  padding: 0;
  line-height: 1;
  font-size: 12px;
  text-align: center;
  width: 100%;
  pointer-events: none;
  border: 0;
  background: none;
}

.acf__admin-icon-deselect {
  font-size: 14px;
  border: 1px solid #fff;
  padding: 10px;
}

.acf__admin-icons-wrap {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 999999;
